.item-wrapper {
  width: 100%;
}
.item-detail {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 200px;
}
.item-detail img{
  width: 100%;
  height: 600px;
  border-radius: 20px;
  margin-left: 30px;
}

.item-txt {
  width: 50%;
  position: relative;
 
}
p {
  width: 400px;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
}
span {
  margin-right: 20px;
}
.button {
  background-color: #ffeeae;
  width: 400px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  margin-left: 40px;
}
.button:hover{color: rgba(0, 0, 255, 0.493);}
.detail-image{width: 50%; margin: 0 auto;}
.detail-image img{
    width: 90%;
    margin-top: 100px;
}
.detail-image:nth-child(3) img{
  width: 100%;
}
.option-title{
  width: 200px;
  font-size: 25px;
  font-weight: bold;
  display: block;
  margin-top: 30px;
}
.option-one{
  width:400px;
}
.option-one p{
  width: 280px;
  font-size: 14px;
  font-weight: 700;
}
.option-two{
  width: 400px;
}
.option-two p{
  width: 280px;
  font-size: 14px;
  font-weight: 700;
}
.option-three{
  width: 280px;
}
.option-three p{
  width: 280px;
  font-size: 14px;
  font-weight: 700;
}
.funding-option{
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  position: relative;
    left: 40px;
}
.funding-option > div {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  cursor: pointer; 
}

.funding-option > div.selected {
  background-color: #ffeeae;
}
.Supplies{margin-left: 50px;}
.Funding-progress{margin-left: 50px;}
.funding-pepole{margin-left: 50px;}
.funding-shop{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left:  40px;
  margin-top: 20px;
}


