body .schoolmain{
    /* display: flex; */
    min-height: 10vh;
    background-color: white;
}


.title{
    text-align: center;
    justify-content: center;
    margin-top: 30px;
    font-size: 30PX;
    color: #FFEEAE;
    cursor: pointer;
}

.local-search-container{
    display: flex;
    align-items: center;
    justify-content: center;  
}
.local-search{
    border-radius: 500px;
    border: solid 1px;
    width: 500px;
    height: 25px;
    padding-left: 10px;
}

.searchbtn{
    width: 30px;
    height: 30px;
    padding-left: 10px;
    cursor: pointer;
}

.left{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 50px;
   
}

.leftphotos{

    width: 400px;
    height: 300px;
    border-radius: 100px;
    margin-left: 200px;
    margin-top: 50px;
    object-fit: cover;
    margin-right: 50px;
}

.left h4 h2{
    margin: 0;
    margin-left: 300px;
    margin-top: 10px;
    padding: 5px 0;
}
.leftphotos{
    cursor: pointer;
}
.left h2{
    cursor: pointer;
}

.text-container{
    display: flex;
    flex-direction: column;
    
}

.fund{
    padding-left: 30px;
}

.footer{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 300px;
    margin-bottom: 0%;
    margin-top: 100%;
    background-color:rgba(0, 0, 0, 0.06);
}



.sns{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    width: 25px;
    height: 25px;
    padding: 5px;
    cursor: pointer;
}
#postbtn{
    background-color: white;
    border: solid 5px #FFEEAE;
    border-radius: 30px;
    color: black;
    cursor: pointer;
    margin-bottom: 10px;
    margin-right: 50px;
}
.postbtn-container{
    display: flex;
    justify-content: end;
}